nav a{
  position: relative;
  padding: 0 10px;
}

nav a::after{
  content: "";
  position: absolute;
  height: 3px;
  background-color: #C004D9;
  width: 0;
  left: 0;
  bottom: -10px;
  transition: ease-in-out .3s;
}

nav a:hover::after{
  width: 100%;
}

.fas.fa-chevron-down::before {
  color: #C004D9;
  font-size: 40px;
  -webkit-animation: down 1.5s infinite;
  animation: down 1.5s infinite;
}

@-webkit-keyframes down {
  from {
    opacity: 0;
    top: -50px;
  }

  to {
    opacity: 1;
    top: -10px;
  }
}

@keyframes down {
  from {
    opacity: 0;
    top: -50px;
  }

  to {
    opacity: 1;
    top: -10px;
  }
}

